.header {
  background: #3f5252;
  justify-content: space-between;
  align-items: center;
  width: 100vw;
  height: 90px;
  padding: 0 55px;
  display: flex;
  position: relative;
}

.header:before {
  content: "";
  z-index: 8;
  background-color: #3f5252;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.header__logo {
  z-index: 10;
  background: url("logo.edbea740.svg") center / contain no-repeat;
  width: 124px;
  height: 37px;
}

.header__hidden-view {
  z-index: 5;
  color: #f1f5f4;
  background-color: #3f5252;
  height: 0;
  margin-top: 103px;
  padding-left: 55px;
  transition: height .3s;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}

.header__hidden-view--active {
  height: calc(100vh - 103px);
}

.header__children-art {
  position: absolute;
  bottom: 0;
  right: 0;
}

.header__intro {
  align-items: start;
  gap: 83px;
  margin-bottom: 70px;
  display: flex;
}

.header__schedule-item:first-of-type, .header__address-item:first-of-type {
  font-size: 14px;
  font-weight: 400;
  line-height: 140%;
}

.header__schedule-item:last-of-type, .header__address-item:last-of-type {
  font-size: 16px;
  font-weight: 600;
  line-height: 150%;
}

.header__navigation {
  margin-bottom: 120px;
  position: relative;
}

.header__navigation:after {
  content: "";
  background-color: #f1f5f4;
  width: 570px;
  height: 1px;
  position: absolute;
  bottom: -60px;
  left: 0;
}

.header__list {
  flex-direction: column;
  gap: 60px;
  height: auto;
  display: flex;
}

.header__element {
  color: #f1f5f4;
  font-family: Playfair, sans-serif;
  font-size: 48px;
  font-weight: 700;
  line-height: 120%;
  position: relative;
}

.header__element:after {
  content: "";
  background-color: #f1f5f4;
  width: 100%;
  height: 1px;
  display: none;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

.header__element:hover .header__element:after {
  display: block;
}

.header__buy-tickets {
  color: #f1f5f4;
  background-color: #cd4d31;
  justify-content: center;
  align-items: center;
  width: 270px;
  height: 60px;
  font-size: 16px;
  font-weight: 600;
  display: flex;
}

.header__right {
  z-index: 10;
  align-items: center;
  gap: 40px;
  display: flex;
}

.header__burger-menu {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 7px;
  width: 25px;
  height: 20px;
  display: flex;
}

.header__burger-menu span {
  transition: transform .3s;
}

.header__burger-menu--active span:nth-of-type(2) {
  display: none;
}

.header__burger-menu--active span:first-of-type {
  transform-origin: center;
  transform: rotate(-45deg)translateY(6px);
}

.header__burger-menu--active span:last-of-type {
  transform-origin: center;
  transform: rotate(45deg)translateY(-6px);
}

.header__burger-menu-part {
  background-color: #fff;
  width: 25px;
  height: 2px;
  display: block;
}

.header__language {
  align-items: center;
  gap: 5px;
  display: flex;
}

.header__ua {
  color: #f1f5f4;
  font-size: 16px;
  font-weight: 500;
  line-height: 150%;
}

.header__arrow-down {
  width: 10px;
  height: 7px;
}

@media screen and (width >= 320px) and (width <= 767px) {
  .header {
    height: 80px;
    padding: 0 20px;
  }

  .header__right {
    flex-direction: row-reverse;
    gap: 30px;
  }

  .header__language {
    display: none;
  }

  .header__language--active {
    display: block;
  }

  .header__hidden-view {
    padding: 0 20px;
    overflow-y: auto;
  }

  .header__hidden-view--active {
    height: calc(100vh - 80px);
  }

  .header__navigation {
    margin-bottom: 100px;
  }

  .header__navigation:after {
    width: 100%;
  }

  .header__intro {
    flex-direction: column;
    gap: 20px;
    margin-bottom: 40px;
  }

  .header__list {
    gap: 20px;
  }

  .header__element {
    font-size: 20px;
    line-height: 150%;
  }

  .header__buy-tickets {
    margin-bottom: 30px;
  }

  .header__children-art {
    max-width: 55%;
    margin: 0 auto;
    display: block;
    position: relative;
  }
}

@media screen and (width >= 768px) and (width <= 1280px) {
